import React from 'react';
import './App.css';
import Question from './Question/Question';
import Button from './Button/Button';



class App extends React.Component{
    constructor(props){
        super(props);
        this.state={
            pushCheck:false
        };
    }
    setPushCheck(){
        this.setState({pushCheck:true});
        console.log('setPushCheckが実行されました');
    }

    render(){
        return(
            <div className='app'>
                <div className='header'></div>

                <div className='main'>
                    <Question
                        questionNumber={1}
                        questionText='好きなアニメのジャンルを教えてください' 
                        option1='選択肢1'
                        option2='選択肢2'
                        option3='選択肢3'
                        option4='選択肢4'
                        val1={1}
                        val2={2}
                        val3={3}
                        val4={4}
                    />

                    <Question
                        questionNumber={2}
                        questionText='質問その2'
                        option1='選択肢1'
                        option2='選択肢2'
                        option3='選択肢3'
                        option4='選択肢4'
                        val1={10}
                        val2={20}
                        val3={30}
                        val4={40}
                        
                    />

                    <Question
                        questionNumber={3}
                        questionText='質問その3'
                        option1='選択肢1'
                        option2='選択肢2'
                        option3='選択肢3'
                        option4='選択肢4'
                        val1={100}
                        val2={200}
                        val3={300}
                        val4={400}
                        
                    />

                    <Question
                        questionNumber={4}
                        questionText='質問その4'
                        option1='選択肢1'
                        option2='選択肢2'
                        option3='選択肢3'
                        option4='選択肢4'
                        val1={1000}
                        val2={2000}
                        val3={3000}
                        val4={4000}
                    />

                    <Button
                        animeName='トモダチコレクション'
                        animeImage=''
                        animeURL='https://taoka8810.github.io/anime_test/'
                    />
                </div>
            </div>

        );
    }
}

export default App;