import React from 'react';
import './Button.css';
import Picture from './anime_pic.jpg';
import '../animations.css';

class Button extends React.Component{
    constructor(props){
        super(props);
        this.state={
            class:'diagnosis-button',
            closeButton:'close-button'
        };
    }
    pushDianosisButton(){
        this.setState({class:'diagnosis-button diagnosis-button-push'});
        const animation1 = document.querySelector('.diagnosis-button:hover');
        animation1.addEventListener('animationend', () => {
            this.setState({class:'change'});
            this.setState({suggestion:'suggestion'});
        });
    }
    pushCloseButton(){
        this.setState({class:'diagnosis-button'});
        this.setState({pushCheck:false});
    }

    render(){
        let layout;
        if(this.state.class === 'change'){
            layout=(
                <div className='suggestion'>
                    <button 
                        className={this.state.closeButton}
                        onClick={() => {this.pushCloseButton()}}>
                        ✕
                    </button>
                    <div className='section'>
                        <h2 className='text1'>あなたのおすすめアニメは・・・</h2>
                        <img src={Picture} alt="アニメの画像" className='anime-picture' />
                        <h1 className='anime-name'>「{this.props.animeName}」です！</h1>
                        <a 
                            href={this.props.animeURL} 
                            className='show-detail-button'>
                            このアニメの詳細を見る
                        </a>
                    </div>
                </div>
            );
        }else{
            layout=(
                <div className='button-wrapper'>
                <button
                    className={this.state.class}
                    onClick={() => {this.pushDianosisButton()}}>
                    診断する
                </button>
            </div>
            );
        }

        return(
            <div className='layout'>
                {layout}
            </div>
            
        );
    }
}

export default Button;