import React from 'react';
import './Question.css';
import '../animations.css';

let val =0;
let answer1 = 0;
let answer2 = 0;
let answer3 = 0;
let answer4 = 0;

class Queation extends React.Component{
    constructor(props){
        super(props);
        this.state={
            option1:'option',
            option2:'option',
            option3:'option',
            option4:'option',
            answer:0
        };
    }
    optionMemory(props){
        if(this.props.questionNumber === 1){
            answer1 = props;
        }else if(this.props.questionNumber === 2){
            answer2 = props;
        }else if(this.props.questionNumber === 3){
            answer3 = props;
        }else if(this.props.questionNumber === 4){
            answer4 = props;
        }
        val = answer1 + answer2 + answer3 + answer4;
        this.setState({answer:val});
    }
    test(){
        console.log('');
        console.log('Question values');
        console.log('val:' + val);
        console.log('answer1:' + answer1);
        console.log('answer2:' + answer2);
        console.log('answer3:' + answer3);
        console.log('answer4:' + answer4);
    }
    option1Clicked(){
        if(this.state.option1==='option'){
            this.setState({option1:'option-checked'});
            this.setState({option2:'option'});
            this.setState({option3:'option'});
            this.setState({option4:'option'});
            this.optionMemory(this.props.val1);
            this.test();
        }
    }
    option2Clicked(){
        if(this.state.option2==='option'){
            this.setState({option2:'option-checked'});
            this.setState({option1:'option'});
            this.setState({option3:'option'});
            this.setState({option4:'option'});
            this.optionMemory(this.props.val2);
            this.test();
        }
    }
    option3Clicked(){
        if(this.state.option3==='option'){
            this.setState({option3:'option-checked'});
            this.setState({option2:'option'});
            this.setState({option1:'option'});
            this.setState({option4:'option'});
            this.optionMemory(this.props.val3);
            this.test();
        }
    }
    option4Clicked(){
        if(this.state.option4==='option'){
            this.setState({option4:'option-checked'});
            this.setState({option2:'option'});
            this.setState({option3:'option'});
            this.setState({option1:'option'});
            this.optionMemory(this.props.val4);
            this.test();
        }
    }
    sendAnswer(){
        return this.state.answer;
    }

    render(){
        return(
            <div className='question'>
                <h1 className='question-text'>{this.props.questionText}</h1>
                <ul className='options'>
                    <label>
                        <li className={this.state.option1}>
                            <input 
                                type='radio'
                                name={this.props.questionNumber}
                                onChange={() => {this.option1Clicked()}}
                                />
                                {this.props.option1}
                        </li>
                    </label>

                    <label>
                        <li className={this.state.option2}>
                            <input 
                                type='radio'
                                name={this.props.questionNumber}
                                onChange={() => {this.option2Clicked()}}
                                />
                                {this.props.option2}
                        </li>
                    </label>

                    <label>
                        <li className={this.state.option3}>
                            <input 
                                type='radio'
                                name={this.props.questionNumber}
                                onChange={() => {this.option3Clicked()}}
                                />
                                {this.props.option3}
                        </li>
                    </label>

                    <label>
                        <li className={this.state.option4}>
                            <input 
                                type='radio'
                                name={this.props.questionNumber}
                                onChange={() => {this.option4Clicked()}}
                                 />
                                {this.props.option4}
                        </li>
                    </label>
                    
                   
                </ul>
            </div>
        );
    }
}

export default Queation;
